import { useState } from 'react';

import FlexBox from 'components/Base/FlexBox';
import FormControl from 'components/Form/FormControl';
import Select from 'components/Listbox/Select';

import { OPTIONS } from './constants';

export default function SelectExample() {
    const [milk, setMilk] = useState('');

    return (
        <FlexBox direction="column" gap={10} customCSS={{ margin: '20px 0' }}>
            <FormControl id="milk" label="" helperText="  " helperTextId="milk-helper-text">
                <Select
                    id="milk"
                    aria-labelledby="milk"
                    name="milk"
                    value={milk}
                    label={OPTIONS.filter((val) => val.value === milk)?.[0]?.label}
                    options={OPTIONS}
                    onChange={(e) => setMilk(e.currentTarget.value)}
                />
            </FormControl>
        </FlexBox>
    );
}
