import FlexBox from 'components/Base/FlexBox';
import Chip from 'components/Button/Chip';

export default function ChipExample() {
    const handleDelete = (value: string | number) => {
        // eslint-disable-next-line no-console
        console.log(value);
    };

    return (
        <FlexBox gap={10} customCSS={{ margin: '20px 0' }}>
            <Chip label="" value="ice cream" isDeletable={false} />
            <Chip label=" " value="deletable" isDeletable onDelete={handleDelete} />
        </FlexBox>
    );
}
