/** @jsxImportSource @emotion/react */
import FlexBox from 'components/Base/FlexBox';

import { css } from '@emotion/react';
import { CSSInterpolation } from '@emotion/serialize';
import palette from 'styles/palette';

type SizeType = 'large' | 'medium' | 'small' | '';
type VariantType = 'outlined' | 'contained' | 'text' | '';

type ButtonType = React.ButtonHTMLAttributes<HTMLButtonElement> & {
    children: React.ReactNode;
    hasStartIcon?: boolean;
    hasEndIcon?: boolean;
    icon?: React.ReactNode;
    type?: 'button' | 'reset' | 'submit';
    size?: SizeType;
    variant?: VariantType;
    isDisabled?: boolean;
    onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
    customCSS?: CSSInterpolation;
};

const buttonStyle = css({ borderRadius: 4 });

const smallStyle = css({
    minHeight: 32,
    height: 'max-content',
    minWidth: 48,
    width: 'max-content',
    padding: '4px 12px',
    fontSize: 13,
    fontWeight: 400,
    '& svg': { width: 14, height: 14 }
});

const mediumStyle = css({
    minHeight: 40,
    height: 'max-content',
    minWidth: 56,
    width: 'max-content',
    fontSize: 14,
    fontWeight: 500,
    padding: '4px 16px',
    '& svg': { width: 16, height: 16 }
});

const largeStyle = css({
    minHeight: 45,
    height: 'max-content',
    minWidth: 96,
    width: 'max-content',
    fontSize: 15,
    fontWeight: 500,
    padding: '4px 16px',
    '& svg': { width: 20, height: 20 }
});

const outlinedStyle = css({
    color: palette.primary.main,
    backgroundColor: '#ffffff',
    border: `1px solid ${palette.primary.main}`,
    '&:hover': {
        color: '#ffffff',
        border: `1px solid ${palette.primary.main}`,
        backgroundColor: palette.lightBlue.main
    },
    '&:focus': {
        border: `1px solid ${palette.secondary.main}`,
        backgroundColor: palette.primary.main
    },
    '&:disabled': {
        border: '1px solid #eeeeee',
        backgroundColor: '#f4f4f4'
    }
});

const containedStyle = css({
    color: palette.secondary.main,
    backgroundColor: palette.lightBlue.main,
    border: `1px solid ${palette.lightBlue.main}`,
    '&:hover': {
        color: '#ffffff',
        backgroundColor: palette.primary.main
    },
    '&:focus': {
        border: `1px solid ${palette.secondary.main}`,
        backgroundColor: palette.secondary.main
    },
    '&:disabled': {
        border: '1px solid #eeeeee',
        backgroundColor: '#145cb1b3'
    }
});

const textStyle = css({
    color: '#000000',
    border: '1px solid #e6e6e6',
    backgroundColor: '#ffffff',
    '&:hover': {
        border: '1px solid #cccccc'
    },
    '&:focus': {
        border: '1px solid #000000'
    },
    '&:disabled': {
        color: '#cccccc',
        border: '1px solid #e6e6e6'
    }
});

function getSizeStyle(size: SizeType) {
    switch (size) {
        case 'large':
            return largeStyle;
        case 'medium':
            return mediumStyle;
        case 'small':
            return smallStyle;
        default:
            return {};
    }
}

function getVariantStyle(variant: VariantType) {
    switch (variant) {
        case 'outlined':
            return outlinedStyle;
        case 'contained':
            return containedStyle;
        case 'text':
            return textStyle;
        default:
            return {};
    }
}

const startIconContainerStyle = css({
    display: 'inline-flex',
    justifyContent: 'center',
    alignItems: 'center',
    width: 20,
    height: 20,
    marginLeft: -2
});

const endIconContainerStyle = css({
    display: 'inline-flex',
    justifyContent: 'center',
    alignItems: 'center',
    width: 20,
    height: 20,
    marginRight: -2
});

/**
 *  [UI Component] Button Component
 *  @param children 
 *  @param hasStartIcon Start Icon  [optional]
 *  @param hasEndIcon End Icon  [optional]
 *  @param icon  [optional]
 *  @param type   (button | reset | submit) [optional]
 *  @param size   ('' | large | medium | small) [optional]
 *  @param variant   ('' | outlined | contained | text) [optional]
 *  @param isDisabled  [optional]
 *  @param onClick Click Event Handler [optional]
 *  @param customCSS  CSS [optional]
 *  @returns JSX.Element
 */
export default function Button(props: ButtonType) {
    const {
        children,
        hasStartIcon = false,
        hasEndIcon = false,
        icon,
        type = 'button',
        size = '',
        variant = '',
        isDisabled = false,
        onClick,
        customCSS,
        ...rest
    } = props;

    const handleClick = (e: React.MouseEvent<HTMLButtonElement>) => {
        e.currentTarget.blur();
        if (onClick) {
            onClick(e);
        }
    };

    return (
        <button
            {...rest}
            // eslint-disable-next-line react/button-has-type
            type={type}
            disabled={isDisabled}
            onClick={handleClick}
            css={css([buttonStyle, getSizeStyle(size), getVariantStyle(variant), customCSS])}
        >
            {hasStartIcon || hasEndIcon ? (
                <FlexBox gap={4} justifyContent="flex-start" alignItems="center">
                    {hasStartIcon && <span css={startIconContainerStyle}>{icon}</span>}
                    <div css={css({ textAlign: 'center' })}>{children}</div>
                    {hasEndIcon && <span css={endIconContainerStyle}>{icon}</span>}
                </FlexBox>
            ) : (
                <div>{children}</div>
            )}
        </button>
    );
}
